import { HTML5 } from 'cc/env';
import { PLATFORM_RUNTIME, PLATFORM_MINIGAME, testconfigName } from './config';

declare const fsUtils: any;
declare const cc: any;

export type PlatformInfo = {
    classList: string[];
    testScript: string[];
    platformIndex: number,
    ip: string;
    port: number;
    timeout: number;
    jobId: string;
};

let platformInfo: PlatformInfo;

export function getPlatformInfo(key: 'all'): PlatformInfo;
export function getPlatformInfo(key: 'classList'): string[];
export function getPlatformInfo(key: 'testScript'): string[];
export function getPlatformInfo(key: 'platformIndex' | 'ip' | 'jobId'): string;
export function getPlatformInfo(key: 'port' | 'timeout'): number;
export function getPlatformInfo(key: keyof PlatformInfo | 'all') {
    console.info(`key ${key}`);
    console.info(`---------platformInfo------,${platformInfo}`);
    if (key === 'all') {
        return platformInfo;
    }
    return platformInfo[key];
}

