export async function getassemblyData() {
    console.log('🚀 ~ file: utils.ts ~ line 75 ~ getassemblyData ~ platformInfo', platformInfo);
    if (platformInfo) return;
    if (!HTML5) {
        console.log('🚀 ~ file: utils.ts ~ line 77 ~ getassemblyData ~ HTML5', HTML5);
        return new Promise((resolve) => {
            const { platforms, jobId, localServer: { ip, port, timeout } } = fsUtils.readJsonSync(testconfigName);
            platformInfo = {
                classList: platforms[0].testScripts.map((v: any) => v.classNames[0]),
                testScript: platforms[0].testScripts.map((v: any) => v.scriptName),
                platformIndex: platforms[0].platformIndex,
                jobId,
                ip,
                port,
                timeout,
            };
            console.log('🚀 ~ file: utils.ts ~ line 80 ~ returnnewPromise ~ platformInfo', JSON.stringify(platformInfo));
            resolve(platformInfo);
        });
    } else {
        return new Promise<void>((resolve, reject) => {
            const xhr = new XMLHttpRequest();
            xhr.responseType = 'text';
            let path = `${testconfigName}?v=${new Date().getTime()}`
            xhr.open('GET', path, true);
            xhr.responseType = 'text';
            xhr.onload = () => {
                let data = JSON.parse(xhr.response);
                console.log("😊 ~ file: utils.ts ~ line 96 ~ testconfig data", data)
                const { platforms, jobId, localServer: { ip, port, timeout } } = data
                platformInfo = {
                    classList: platforms[0].testScripts.map((v: any) => v.classNames[0]),
                    testScript: platforms[0].testScripts.map((v: any) => v.scriptName),
                    platformIndex: platforms[0].platformIndex,
                    jobId,
                    ip,
                    port,
                    timeout,
                };
                resolve();
            };
            xhr.onerror = () => {
                reject(new Error('request get testconfig failed!'));
            };
            xhr.send(null);
        })
    }
}

export function isArrayBuffer(val: any): val is ArrayBuffer {
    return (Object.prototype.toString.call(val) === '[object ArrayBuffer]') || ArrayBuffer.isView(val);
}

export function promisify<T>(fn: any): () => Promise<T> {
    if (typeof fn === 'function') {
        return (...arg: any[]) => {
            const res = fn(...arg);
            if (res instanceof Promise) {
                return res;
            } else {
                return Promise.resolve(res);
            }
        };
    }
    return () => Promise.resolve(fn);
}

export function isObject(val: any): val is object {
    return typeof val === 'object' && val !== null;
}

/**
 * 判断对象为空
 * @param obj
 * @returns
 */
export function isEmptyObject(obj: any): boolean {
    for (const i in obj) {
        return false;
    }
    return true;
}

/**
 * 增加延迟
 * @param num
 * @returns
 */
export async function sleep(num: number) {
    return new Promise((resolve) => {
        // return / await 等待执行完
        setTimeout(() => {
            resolve('延迟');
        }, num * 1000);
    });
}

export function isMiniGame() {
    console.info(`current platform is ${cc.sys.platform}`);
    if (cc.sys.platform in PLATFORM_RUNTIME || cc.sys.platform in PLATFORM_MINIGAME) {
        return true;
    }
}

