export type BaseInfo = {
    testScript: string;
    className: string;
    functionName: string;
};

export type ImgInfo = BaseInfo & {
    width: number;
    height: number;
    imageData: Uint8Array;
    imageName: string;
};

export type ResultInfo = {
    resultDetail: string;
    testTimer: number;
    logs: string;
    assertResult: string;
    _idx?: string;
};

export interface finallyInfo extends ImgInfo, ResultInfo {
    imgIdx: number;
}

