import { getPlatformInfo } from "./utils";
import { StateCode } from './config';
import { imgList } from "./capture";

let ws: WebSocket | null;
let timer: NodeJS.Timeout;
let connected = false;
let tryCount = 0;
let reConnect: () => void;
let msgId = 0;
const resolveObj: Map<number, [() => void, (v: unknown) => void]> = new Map();

export function createWebsoket(host = '127.0.0.1', port = 8000) {
    return new Promise((resolve) => {
        console.log('createWebsoket', `ws://${host}:${port}/ws/caster`);
        if (ws) return ws;
        reConnect = () => {
            console.log('reConnect');
            ws = new WebSocket(`ws://${host}:${port}/ws/caster`);
            initSocket(resolve);
        };
        reConnect();
    });
}

export function closeWebSocket() {
    ws && ws.close();
    timer && clearInterval(timer);
}

async function initSocket(resolve: (value: unknown) => void) {
    if (!ws) return;
    console.log('initSocket');
    ws.onopen = () => {
        console.info('socket connected ~~~');
        connected = true;
        tryCount = 0;
        resolve('');
    };
    ws.onmessage = (event) => {
        const { id, state, message } = JSON.parse(event.data);
        if (['Image_Ok', 'Msg_Ok'].some(val => val === state) && id) {
            resolveObj.get(Number(id))![1]('');
            resolveObj.delete(id);
        }
    };
    ws.onerror = () => {
        console.info('socket disconnect & retry ~~~');
        connected = false;
        ws = null;
        if (++tryCount <= 5) {
            reConnect && reConnect();
        } else {
            console.error('【automation error】 socket disconnect on error');
        }
    };
    ws.onclose = () => {
        console.info('socket closed ~~~');
        ws = null;
        connected = false;
    };
}

function heartBeat() {
    console.log("🚀 ~ file: client.ts ~ line 73 ~ heartBeat ~ heartBeat");
    const { platformIndex, jobId } = getPlatformInfo('all');
    // timer && clearTimeout(timer);
    ws?.send(`type:ping_${jobId}_${platformIndex}`);
    timer = setInterval(() => {
        ws?.send(`type:ping_${jobId}_${platformIndex}`);
    }, 10000);
}

export function postMsgObj(message: { id?: number, [k: string]: any; }, resolve?: (v: unknown) => void) {
    if (!ws) return;
    if (!message.id) {
        message.id = ++msgId;
    }
    ws.send(JSON.stringify(message));
    if (resolve) {
        resolveObj.set(msgId, [() => postMsgObj(message), resolve]);
    }
}

export function postBuffer(buffer: ArrayBuffer, nowMsgId?: number, resolve?: (val: unknown) => void) {
    ws?.send(buffer);
    if (nowMsgId && resolve) {
        resolveObj.set(nowMsgId, [() => postBuffer(buffer), resolve]);
    }
}

export function startTest(platformIndex: number, logs: string) {
    return new Promise((resolve) => {
        const obj = {
            state: StateCode.START,
            platformIndex,
            jobId: getPlatformInfo('jobId'),
            logs
        };
        // resolveObj.startTest = [() => { postMsgObj(obj) }, resolve]
        clearInterval(timer);
        heartBeat();
        postMsgObj(obj, resolve);
    });
}

export function endTest(platformIndex: number, logs: string) {
    return new Promise((resolve) => {

        const obj = {
            state: StateCode.END,
            platformIndex,
            jobId: getPlatformInfo('jobId'),
            logs,
            imgList
        };
        console.log("🚀 ~ file: client.ts ~ line 137 ~ returnnewPromise ~ imgList", obj.jobId);
        clearInterval(timer);
        postMsgObj(obj, resolve);
    });
}

export function getNowMsgId(): number {
    return ++msgId;
}