import { getNowMsgId, postBuffer } from './client';
import { ResultCode } from './config';
import { finallyInfo, ImgInfo, nowImgInfo, ResultInfo } from './result-manage';
import { getPlatformInfo } from './utils';

// 声明cc模块
// import cc from 'cc'
declare var cc: any;

let nowIndex = 0;

export const imgList: Record<string, Record<string, number>> = {};

export async function captureScreen(imageName?: string): Promise<ImgInfo> {
    return new Promise<any>(async (resolve, reject) => {
        const { width, height } = cc.screen.resolution;
        const { className, functionName } = nowImgInfo;
        const cameras = cc.director.getScene()!.getComponentsInChildren(cc.CameraComponent);
        const renderTexture = new cc.RenderTexture();

        renderTexture.reset({
            width,
            height
        });

        cameras.forEach((camera: any) => {
            if (!camera.targetTexture) {
                camera.targetTexture = renderTexture;
            }
        });

        await waitForNextFrame();

        cameras.forEach((camera: any) => {
            if (camera.targetTexture === renderTexture) {
                camera.targetTexture = null;
            }
        });

        const imageData: Uint8Array = renderTexture.readPixels();

        if (imageData.byteLength !== parseInt(width) * parseInt(height) * 4) {
            return reject('readPixels is not accord with width * height * 4');
        }

        const imgInfo: ImgInfo = Object.assign(nowImgInfo, {
            width: parseInt(width),
            height: parseInt(height),
            imageName: `${className}_${functionName}${imageName ? `_${imageName}` : ''}`,
            imageData
        });

        renderTexture.destroy();

        resolve(imgInfo);
    });
}

export async function waitForNextFrame() {
    return new Promise<void>((resolve, reject) => {
        cc.director.on;
        cc.director.once(cc.Director.EVENT_END_FRAME, () => {
            resolve();
        });
    });
}

/**
 * 截取一张图片，保存相应信息
 */
export async function captureOneImage(imageName?: string) {

    nowIndex++;

    const startTime = performance.now();
    const imgInfo = await captureScreen(imageName);

    const resultInfo: ResultInfo = {
        resultDetail: 'This is a image!',
        logs: '',
        testTimer: performance.now() - startTime,
        assertResult: ResultCode.PASS,
    };

    const obj: finallyInfo = { ...imgInfo, ...resultInfo, imgIdx: nowIndex };

    // if (hasCanvas()) {
    //     return getImageBase(obj.width, obj.height, obj.imageData, imageName!)
    // }

    await addImgInfo(obj);


    return imgInfo;

}

/**
 *  场景图片索引归零
 */
export function resetNowIndex() {
    nowIndex = 0;
}

async function addImgInfo(obj: finallyInfo) {
    const { jobId, platformIndex } = getPlatformInfo('all');
    const { testScript, className, functionName, imageName, testTimer, width, height, imgIdx, imageData } = obj;
    const iinfo = [jobId, platformIndex, testScript, className, functionName, `${imageName}_${imgIdx}`, testTimer, width, height, true].join(',');
    const byteArr = new ArrayBuffer(2 + iinfo.length + obj.imageData.length);

    if (!imgList[className]) {
        imgList[className] = {};
    }
    if (!imgList[className][functionName]) {
        imgList[className][functionName] = 0;
    }
    imgList[className][functionName]++;

    const dv = new DataView(byteArr);
    dv.setUint8(0, iinfo.length);

    for (let i = 0; i < iinfo.length; i++) {
        dv.setUint8(2 + i, iinfo[i].charCodeAt(0));
    };

    new Uint8Array(byteArr).set(imageData, 2 + iinfo.length);

    await sliceBuffer(byteArr, platformIndex, `${imageName}_${imgIdx}`);

}

async function sliceBuffer(byteArr: ArrayBuffer, platformIndex: number, imageName: string) {
    const length = byteArr.byteLength;
    const chunkSize = 1048576;
    const nowMsgId = getNowMsgId();
    const jobId = getPlatformInfo('jobId');
    const chunks = Math.ceil(length / chunkSize);

    for (let i = 0; i < chunks; i++) {

        const start = i * chunkSize;
        const end = Math.min(length, start + chunkSize);
        const chunkInfo = [jobId, platformIndex, nowIndex, length, chunkSize, chunks, i, start, end, nowMsgId, imageName].join(',');

        const sliceBuffer = new Uint8Array(byteArr, start, end - start);

        const newBuffer = new ArrayBuffer(2 + chunkInfo.length + sliceBuffer.length);
        const dv = new DataView(newBuffer);
        dv.setUint8(0, chunkInfo.length);
        for (let i = 0; i < chunkInfo.length; i++) {
            dv.setUint8(2 + i, chunkInfo[i].charCodeAt(0));
        }
        new Uint8Array(newBuffer).set(sliceBuffer, 2 + chunkInfo.length);

        await sendImg(newBuffer, nowMsgId).catch(err => console.error(`【automation error】 ${imageName}.png chunks ${i} not send seccess`));
    }
}

function sendImg(newBuffer: ArrayBuffer, nowMsgId: number) {
    let timer: NodeJS.Timeout;
    return new Promise((resolve, reject) => {
        postBuffer(newBuffer, nowMsgId, resolve);
        timer = setTimeout(() => {
            reject('now Buffer is not success');
        }, 5000);
    }).then(() => {
        clearTimeout(timer);
    });
}

