import { runAllTestObj } from './fixture';
import { getassemblyData, getPlatformInfo } from './utils';
import resetApiFn, { post, request } from './reset-api';
import { createWebsoket, endTest, startTest } from './client';
import { initJson } from './collection-init-info';

// 声明cc模块
declare let cc: any;

/**
 * RunTest - 测试流程
 */
class TestRunner {
    async run() {
        console.info('🚀 ~ file: runner.ts ~ line 26 ~ TestRunner ~ run ~ run');
        /**
         * 测试前 -- 数据初始化
         */

        await getassemblyData();
        console.log('🚀 ~ file: runner.ts ~ line 35 ~ TestRunner ~ run ~ getassemblyData');

        // post('nowLog', {
        //     msg: 'runner'
        // });

        cc.profiler.hideStats();

        try {
            const { platformIndex, classList, testScript, ip, port, timeout, jobId } = getPlatformInfo('all');
            console.log('🚀 ~ file: runner.ts ~ line 36 ~ TestRunner ~ run ~ ip', ip);
            console.log('🚀 ~ file: runner.ts ~ line 36 ~ TestRunner ~ run ~ port', port);
            console.log('🚀 ~ file: runner.ts ~ line 36 ~ TestRunner ~ run ~ classList', classList);
            // if (classList.length === 0) cc.game.end();
            resetApiFn();

            initJson.platformIndex = platformIndex;
            console.info('🚀 ~ file: runner.ts ~ line 56 ~ TestRunner ~ run ~ initJson.platformIndex', initJson.platformIndex);
            console.info(`init json------------------`, initJson);
            // await request('addPlatform', { platformIndex, jobId });
            console.info('addPlatform', initJson);

            await createWebsoket(ip, port);
            console.info('createWebsoket');

            await startTest(platformIndex, 'Test Start');
            console.info('startTest');

            await runAllTestObj(jobId, platformIndex, classList, testScript);

            await endTest(platformIndex, 'Test End');

            // cc.game.end();
        } catch (error: any) {
            console.info('🚀 ~ file: runner.ts ~ line 82 ~ TestRunner ~ run ~ error');

            console.error(`【automation error】 ${error.stack ? error.stack : error}`);

            // cc.game.end();
        }
    }
}

