import { getassemblyData, getPlatformInfo } from "./utils";

export let initJson: InitJson = {
    platformIndex: 0,
    sceneList: [],
    classList: {}
};

type InitJson = {
    platformIndex: number;
    sceneList: string[];
    classList: Record<string, TestClassInfo>;
};

type TestClassInfo = {
    name: string;
    scene: string;
    caseList: Record<string, []>;
};

export async function addTestClass(obj: TestClassInfo) {
    initJson.classList[obj.name] = obj;
    initJson.sceneList.push(obj.scene);
}
