import { rejects } from 'assert';
import { resolve } from 'path';
import { getPlatformInfo, isMiniGame } from './utils';

declare const cc: any;

export default () => {
    console.log('🚀 ~ file: reset-api.ts ~ line 6 ~ platformIndex');
    const { ip, port, platformIndex, jobId } = getPlatformInfo('all');
    console.log('🚀 ~ file: reset-api.ts ~ line 10 ~ platformIndex', platformIndex);
    const baseUrl = `http://${ip}${port ? `:${port}` : ''}`;
    // fetch(`${baseUrl}/sys/getDeviceListState`);
    const { log, error, timeEnd } = window.console;
    window.console.log = async (...arg) => {
        log(...arg);
        const str = arg.join(' ');
        await request(`runtime/log`, {
            type: 'info',
            msg: str,
            platformIndex,
            jobId,
        });
    };
    window.console.error = async (...arg) => {
        error(...arg);
        const str = arg.join(' ');
        await request(`runtime/log`, {
            type: 'error',
            msg: str,
            platformIndex,
            jobId,
        });
    };
    window.addEventListener('error', (e) => { console.error(e.message); });

    window.console.timeEnd = async (str) => {
        timeEnd(str);
        if (str?.startsWith('LoadScene')) {
            await request(`runtime/log`, {
                type: 'info',
                msg: str,
                platformIndex,
                jobId,
            });
        }
    };

    cc.debug._resetDebugSetting(2);
};


export function post(url: string, body: any) {
    return new Promise<void>((resolve, reject) => {
        const ip = getPlatformInfo('ip');
        const port = getPlatformInfo('port');
        const baseUrl = `http://${ip}${port ? `:${port}` : ''}`;
        url = `${baseUrl}/${url}`;
        const xhr = new XMLHttpRequest();
        xhr.open('POST', url, true); //同步的 async 在微信小游戏中不支持
        xhr.setRequestHeader('content-type', 'application/json');
        xhr.send(JSON.stringify(body));
        xhr.onload = () => {
            if (xhr.status === 200 || xhr.status === 0) {
                console.log("😊 ~ file: reset-api.ts ~ line 61 ~ xhr.response", xhr.response)
                resolve(xhr.response);
            }
            if (xhr.status === 400) {
                reject(`request is bad,${xhr.responseText}`);
            }
            if (xhr.status >= 500) {
                reject(`server error,${xhr.responseText}`);
            }
        };
        xhr.onerror = () => {
            reject(`post error,url= ${url}`);
        };
    });
}

export async function request(url: string, body: object): Promise<any> {
    // eslint-disable-next-line @typescript-eslint/no-floating-promises
    console.info('request请求', url)
    const ip = getPlatformInfo('ip');
    const port = getPlatformInfo('port');
    const baseUrl = `http://${ip}${port ? `:${port}` : ''}`;
    if (isMiniGame()) {
        console.info('is mini game############')
        await post(url, body).catch((error) =>
            console.info(`post request mini game error,${error}`))
    } else {
        console.info('fetch ############', url)
        return fetch(`${baseUrl}/${url}`, {
            method: 'post',
            body: JSON.stringify(body)
        });
    }

}

