import { post } from './reset-api';
import { getPlatformInfo } from './utils';

// 声明cc模块
// import cc from 'cc'
declare let cc: any;
/**
 * 加载场景
 */
export function loadScene(sceneName: string, scriptName: string) {
    const { platformIndex, jobId } = getPlatformInfo('all');
    post('runtime/nowScript', { platformIndex, sceneName, scriptName, jobId });
    return new Promise<void>((resolve, reject) => {
        cc.director.loadScene(sceneName, (err: any) => {
            if (err) {
                reject(err);
            } else {
                resolve();
            }
        });
    });
}

/**
 * 运行新场景之后所触发的事件
 */
export function ensureFirstSceneLaunched(): Promise<void> {
    return new Promise((resolve) => {
        cc.director.once(cc.Director.EVENT_AFTER_SCENE_LAUNCH, () => {
            resolve();
        });
    });
}

